.projects-content {
    background-color: #05001742;
}

.projects {
    position: relative;
    top: 100px;
    width: 90%;
    min-height: 120vh;
    overflow: auto;
    margin: auto;
}

.projects-cards {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}

.p-card {
    cursor: pointer;
    position: relative;
    border: .1px solid sandybrown;
    border-radius: 12px;
    width: calc(90% / 4);
    min-width: 250px;
    height: 150px;
    margin: 2rem 1%;
    position: static;
    transition: transform .5s ease-in-out;
}

.p-card:nth-child(3) {
    background-color: #0f0e13;
    background-image: radial-gradient(at 0% 0%, hsla(253, 16%, 7%, 1) 0, transparent 50%), radial-gradient(at 50% 0%, rgb(90, 47, 106) 0, transparent 50%), radial-gradient(at 100% 0%, hsla(339, 49%, 30%, 1) 0, transparent 50%);
}

.p-card:nth-child(1) {
    background-color: #0f0e13;
    background-image: radial-gradient(at 0% 100%, hsla(253, 16%, 7%, 1) 0, transparent 50%), radial-gradient(at 50% 0%, hsla(225, 39%, 25%, 1) 0, transparent 50%);
}

.p-card:nth-child(2) {
    background-color: #0f0e13;
    background-image: radial-gradient(at 0% 0%, hsla(253, 16%, 7%, 1) 0, transparent 50%), radial-gradient(at 50% 0%, rgb(56, 106, 47) 0, transparent 50%), radial-gradient(at 100% 0%, hsla(339, 49%, 30%, 1) 0, transparent 50%);
}

.p-card:nth-child(4) {
    background-color: #0f0e13;
    background-image: radial-gradient(at 0% 0%, hsla(253, 16%, 7%, 1) 0, transparent 50%), radial-gradient(at 50% 0%, hsla(225, 39%, 30%, 1) 0, transparent 50%), radial-gradient(at 100% 0%, hsla(339, 49%, 30%, 1) 0, transparent 50%);
}

.p-card:hover {
    transform: scaleY(1.7) scaleX(1.4);
    text-transform: none;
}

.p-card:nth-child(4):hover {
    transform: scaleY(1.5);
    cursor: pointer;
    text-transform: none;
}

.p-card:nth-child(1):hover {
    transform: scaleY(1.5);
    text-transform: none;
}

.p-card-head-l {
    margin: 5px;
    padding: 10px;
    border: 4px solid #fff;
    border-radius: 50%;
}

.p-card-body {
    position: relative;
    height: 80px;
}

.text-gold {
    color: #f06000;
}

.p-card img {
    width: 100%;
    height: 150px;
}

.infos-pj {
    position: absolute;
    right: 7%;
    bottom: 0%;
}

.infos-pj i {
    color: #f06000;
    font-size: x-large;
    transition: all ease .5s;
}

.infos-pj i:hover {
    color: #124f8c;
}

.show-projet-details {
    width: 100%;
    position: absolute;
    height: 100%;
    min-height: 130vh;
    border: #f06000 solid 2px;
    background-color: rgba(0, 0, 0, 0.899);
    top: 20px;
    transition: all .5s ease-in-out;
}

.content-des-pj {
    position: relative;
    width: 80%;
    margin: auto;
    height: 90vh;
    top: 90px;
}

.show-projet-img {
    margin: auto;
    color: #fff;
    width: 90%;
    background-color: rgba(255, 255, 255, 0.477);
    border: #fff solid;
}

.show-projet-img>img {
    width: 100%;
    max-height: 380px;
}

.pj-des {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: auto;
    color: #fff;
    background-color: rgba(240, 248, 255, 0.012);
    width: 90%;
    border: #fff solid;
    border-top: none;
}

.technos {
    display: flex;
    flex-wrap: wrap;
    width: 50%;
}

.pj-ds-ds {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 45%;
    text-align: center;
    line-height: 22px;
}

.pj-name {
    width: 100%;
    padding: 10px;
    margin-bottom: 1rem;
}

.technos>div {
    border: #124f8c solid;
    margin: auto;
    padding: 1rem;
    border-radius: 1rem;
}

.visit {
    background-color: #087ef4e3;
    padding: .8rem;
    border-radius: 1rem;
}