* {
    margin: 0;
    padding: 0;
    /* font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif; */
    font-family: Whitney SSm A, Whitney SSm B, Helvetica Neue, Helvetica, Arial, sans-serif;
}

.aside {
    overflow: hidden;
}

.soon,
.aside-content,
.aside {
    min-height: 100vh;
}

.aside-content div {
    padding: .5rem;
    font-size: 2rem;
}

.quit {
    width: 100%;
    background-color: #ff20039d;
}

a {
    text-decoration: none;
}

body {
    /* background: url("../public/Images/fond.jpg"); */
    background-size: cover;
    /* background-repeat: no-repeat; */
    background-position: center;
}

.flex {
    display: flex;
}

h1 {
    margin: 1rem 0;
}

.mrgtop-1 {
    margin-top: 1rem;
}

:root {
    --col1: calc(100%/12);
    --heigtvh: calc(100vh + 30px);
}

.toggle {
    display: none;
}

.space-around {
    justify-content: space-around;
}

.header {
    width: 100%;
    /* background-color: #000819; */
    background-color: #0f0e13;
    background-image: radial-gradient(at 0% 0%, hsla(253, 16%, 7%, 1) 0, transparent 50%), radial-gradient(at 50% 0%, hsla(225, 39%, 30%, 1) 0, transparent 50%), radial-gradient(at 100% 0%, hsla(339, 49%, 30%, 1) 0, transparent 50%);
    position: fixed;
    z-index: 1;
}

.column {
    display: flex;
    flex-direction: column;
}

.align-center {
    align-items: center;
}

.j-center {
    justify-content: center;
}

.hide {
    display: none;
}

a,
.text-light {
    color: #ffffff !important;
}

.btn {
    padding: .5rem 0;
    position: absolute;
}

.btn:hover {
    cursor: pointer;
}

.relative {
    position: relative;
}

.btn-bottom {
    bottom: 0;
}

.c-yellow {
    color: #f6fa00;
}

.primary {
    background-color: #011135;
    font-size: 1.6rem;
    margin-bottom: 1rem;
    border: 1px rgb(225, 117, 2) solid;
    margin-top: 1rem;
}

.primary:hover {
    border: 1px #03eeff solid;
}

.bnt-transparent {
    background: transparent;
}

.text-center {
    text-align: center;
}

.row {
    display: flex;
    flex-wrap: wrap;
}

.f-end {
    justify-content: flex-end;
}

.f-start {
    justify-content: flex-start;
}

.space-between {
    justify-content: space-between;
}

ul {
    list-style-type: none;
}

.link:hover {
    cursor: pointer;
}

.toggle i {
    font-size: 2rem;
}

.col-1 {
    width: var(--col1);
}

.col-2 {
    width: calc(var(--col1)*2);
}

.col-3 {
    width: calc(var(--col1)*3);
}

.col-4 {
    width: calc(var(--col1)*4);
}

.col-5 {
    width: calc(var(--col1)*5);
}

.col-6 {
    width: calc(var(--col1)*6);
}

.col-7 {
    width: calc(var(--col1)*7);
}

.col-8 {
    width: calc(var(--col1)*8);
}

.col-9 {
    width: calc(var(--col1)*9);
}

.col-10 {
    width: calc(var(--col1)*10);
}

.col-11 {
    width: calc(var(--col1)*11);
}

.col-12 {
    width: 100%;
}

.c-red {
    color: #910000;
}

.c_brown {
    color: rgb(37, 1, 1);
}

.c-orange {
    color: #f17500;
}

.c-blue {
    color: #00b7ff;
}

.c-gray {
    color: #ccc;
}

.c-head {
    color: #011135;
}

.c-pinky {
    color: #ff034ffa;
}

.small-profile {
    width: 2.5rem;
    height: 2.5rem;
}

.profile li {
    padding: 2px;
}

.border {
    border: .5px solid gray;
}

.around {
    border-radius: 50%;
}

.filter {
    position: absolute;
    background-color: #012f6477;
    height: 100%;
}

.experiences {
    margin: 2rem auto;
}

.skills,
.about,
.careers,
.experiences,
.slider {
    position: relative;
    border-bottom: 1px #350b01 solid;
}

.about {
    min-height: var(--heigtvh);
}

.skills {
    min-height: 28rem;
    background-image: radial-gradient(at 0% 0%, hsla(253, 16%, 7%, 1) 0, transparent 50%), radial-gradient(at 50% 0%, hsla(225, 39%, 30%, 1) 0, transparent 50%), radial-gradient(at 100% 0%, hsla(339, 49%, 30%, 1) 0, transparent 50%);
}

.about {
    min-height: calc(100vh + 3rem);
}

.h1-title {
    padding-top: 2rem;
}

.footer {
    position: relative;
    /* background: #000819; */
    background-color: #0f0e13;
    background-image: radial-gradient(at 0% 0%, hsla(253, 16%, 7%, 1) 0, transparent 50%), radial-gradient(at 50% 0%, hsla(225, 39%, 30%, 1) 0, transparent 50%), radial-gradient(at 100% 0%, hsla(339, 49%, 30%, 1) 0, transparent 50%);
    min-height: 6rem;
}

.fs-2 {
    font-size: 2.2rem;
}

.p-des {
    padding: .5rem;
}

@media screen and (max-width:850px) {
    .p-des {
        font-size: .9rem;
    }
    .link {
        font-size: .7rem;
    }
    h1 {
        font-size: 1.5rem;
    }
    h3 {
        font-size: 1.2rem;
    }
    .primary {
        font-size: 1.3rem;
    }
    .skill-type {
        font-size: 2rem !important;
    }
    .absolute {
        position: absolute;
        top: 90%;
    }
    .fs-2 {
        font-size: 2rem;
    }
    .skill-card {
        width: 23%;
        max-height: 8rem;
    }
    .skills-content {
        min-height: 15rem;
    }
}

@media screen and (max-width:600px) {
    .header {
        font-size: .7rem;
    }
    h1 {
        font-size: 1.2rem;
    }
    h3 {
        font-size: 1rem;
    }
    .card {
        border: .2px solid #f7c184;
        margin: .3rem 0;
    }
    .p-des {
        font-size: .8rem;
    }
    p {
        font-size: .8rem;
    }
    .primary {
        font-size: 1rem;
    }
    .profile-img {
        min-width: 400px;
    }
    .knowme {
        min-width: 400px;
    }
    .skill-type {
        min-height: 15rem;
        font-size: 1.5rem !important;
    }
    .fs-2 {
        font-size: 1.6rem;
    }
    .s-directions {
        display: none;
    }
    .hello {
        font-size: 1.4rem;
    }
}

@media screen and (max-width:500px) {
    .toggle {
        display: flex;
    }
    .navs {
        width: 2%;
    }
    .ul-navs {
        display: none;
    }
    .header {
        font-size: .8rem;
    }
    .profile {
        width: calc(var(--col1)*8);
    }
    h1 {
        font-size: 1rem !important;
    }
    h3 {
        font-size: .8rem !important;
    }
    .s-left {
        left: 5px !important;
    }
    .s-right {
        right: 5px !important;
    }
    .profile-img {
        width: 100%;
        min-width: 300px;
    }
    .knowme {
        width: 100%;
        min-width: 300px;
    }
    .primary {
        font-size: .8rem;
    }
    .small-profile {
        width: 2rem;
        height: 2rem;
        border-radius: 50%;
    }
    .skill-type {
        font-size: 1.2rem !important;
    }
    .fs-2 {
        font-size: 1.3rem;
    }
    .p-des {
        font-size: .7rem;
    }
    .hello {
        font-size: 1.2rem;
    }
    .square {
        width: 36%;
    }
    .skills-content {
        min-height: 10rem;
    }
    .skill-card {
        max-height: 5rem;
    }
}

@media screen and (max-width:350px) {
    .skills {
        min-height: 16rem;
    }
    h1 {
        font-size: .8rem !important;
    }
    h3 {
        font-size: .7rem !important;
    }
    .contact-elt h3 {
        font-size: .6rem;
    }
    .s-left {
        left: 5px !important;
    }
    .s-right {
        right: 5px !important;
    }
    p {
        font-size: .55rem !important;
    }
    .profile-img {
        width: 100%;
        min-width: 200px;
    }
    .knowme {
        width: 100%;
        min-width: 200px;
    }
    .primary {
        font-size: .6rem;
    }
    .skill-type {
        min-height: 8rem;
        font-size: 1rem !important;
    }
    .fs-2 {
        font-size: 1.1rem;
    }
    .p-des {
        font-size: .7rem !important;
    }
    .hello {
        font-size: 1rem;
    }
    .square {
        margin: auto;
        width: 70%;
    }
}

.dark {
    /* background: #000819 !important; */
    background-color: #0f0e13;
    background-image: radial-gradient(at 0% 0%, hsla(253, 16%, 7%, 1) 0, transparent 50%), radial-gradient(at 50% 0%, hsla(225, 39%, 30%, 1) 0, transparent 50%), radial-gradient(at 100% 0%, hsla(339, 49%, 30%, 1) 0, transparent 50%);
    color: #ffffff !important;
}

.text-dark {
    color: #000;
}