.slider {
    width: 100%;
    position: relative;
    overflow: hidden;
    cursor: pointer;
    /* background-color: #04439b73; */
    max-height: 100vh;
    margin-bottom: 1rem;
}

.bg-head {
    background-color: #02d3f7;
    min-height: 1rem;
}

.s-des {
    background-color: #0269991e;
}

.s-des h3 {
    font-size: 2rem;
    padding-top: 2rem;
}

.slide-l {
    /* min-height: 70vh; */
    /* background: linear-gradient(#011135, #023099, #0439b4); */
    background-color: #0f0e13;
    background-image: radial-gradient(at 0% 0%, hsla(253, 16%, 7%, 1) 0, transparent 50%), radial-gradient(at 50% 0%, hsla(225, 39%, 30%, 1) 0, transparent 50%), radial-gradient(at 100% 0%, hsla(339, 49%, 30%, 1) 0, transparent 50%);
    width: 50%;
}

.default-txt {
    color: #011135;
}

.contents {
    transition: transform ease-in-out 1s;
}

.s-content {
    position: relative;
    display: flex;
    width: 100%;
    flex: 1 0 100%;
    justify-content: center;
    /* height: 350px; */
}

.s-right,
.s-left {
    position: absolute;
    top: 50%;
    font-size: 2rem;
    transform: translateY(-50%);
    cursor: pointer;
    color: #ffffff;
}

.s-left {
    left: 20px;
}

.s-right {
    right: 20px;
}

.s-content img {
    max-height: 250px;
}

.s-content div {
    margin: 1.5rem 0;
}

.my-name {
    font-size: 5rem;
}

.phoenix-coder {
    padding-top: 2rem;
    z-index: -1;
}

.bg-dk {
    color: #ffffff !important;
    background-color: #0f0e13;
    background-image: radial-gradient(at 0% 0%, hsla(253, 16%, 7%, 1) 0, transparent 50%), radial-gradient(at 50% 0%, hsla(225, 39%, 30%, 1) 0, transparent 50%), radial-gradient(at 100% 0%, hsla(339, 49%, 30%, 1) 0, transparent 50%);
}