.skill-containt {
    flex-wrap: wrap;
}

.text-dark {
    color: #000000;
}

.skill-card,
.card {
    border: inset #f7c184;
    margin: .2rem 0;
    border-radius: 5px;
    background-image: radial-gradient(at 0% 0%, hsla(253, 16%, 7%, 1) 0, transparent 50%), radial-gradient(at 50% 0%, hsla(225, 39%, 30%, 1) 0, transparent 50%), radial-gradient(at 100% 0%, hsla(339, 49%, 30%, 1) 0, transparent 50%);
}

.container {
    display: flex;
    flex-direction: column;
    align-content: center;
    margin: 0 2rem;
}

.skill {
    overflow-x: auto;
    margin-bottom: 1rem;
    /* background: linear-gradient(#6025ceaf, #2388db96, #6025ceaf);
    background: linear-gradient(#2388db96, #21ebfd73, #0955b93a); */
}

.b-left {
    border-left: 2px #0244df solid;
}

.b-right {
    border-right: 2px #011135 solid;
}

.b-btm {
    margin-top: .5rem;
    border-bottom: .8px #011135 solid;
    padding: .5rem 0;
}

.title {
    /* background: linear-gradient(#067563ca, #34c9f7bd, #ffcd28c9); */
    color: #000000;
    padding: .5rem;
    margin-top: 1rem;
}

.c-cv {
    color: #FF9408;
}

.hello {
    right: 0;
}

.about {
    /* margin-top: 2rem; */
    /* background: url('../public/Images/fnd..png') no-repeat; */
    background-size: cover;
    /* box-shadow: 1px 1px rgb(37, 1, 1), -1.2em 0 .1em rgb(0, 70, 128); */
}

.aboutimg img {
    border-radius: 5px;
}

.experience {
    position: relative;
    margin: 1rem 0;
    box-shadow: 10px 5px 5px rgb(0, 0, 0);
}

.padding0-1 {
    padding: 0 1rem;
}

.xper {
    height: 10rem;
    background-size: contain;
    background-repeat: no-repeat;
}

.border-radius {
    border-radius: 5px;
}

.date {
    position: absolute;
    right: 1%;
    bottom: 1%;
}

.float-r {
    position: absolute;
    float: right;
    right: 1%;
}

.up {
    font-size: 2rem;
}

.up:hover {
    cursor: pointer;
    background-color: #f7c184;
    padding: 3px;
}

.pointer {
    cursor: pointer;
}

.dropdown {
    display: inline-block;
    position: relative;
}

.dropdown-content {
    position: absolute;
    display: none;
    z-index: 1;
    margin: 0;
    padding: .5rem .5rem;
}

.lang-active {
    background-color: #34a9f7;
}

.dropdown-content li:hover {
    background-color: #d4c98b;
}

.dropdown:hover .dropdown-content {
    display: block;
}

.icon {
    max-width: 1.8rem;
}

.profile-img {
    min-width: 300px;
}

.aboutimg {
    border: #011135 solid;
}

.knowme {
    min-width: 200px;
}

.skill-type {
    color: #ffffff;
    background: url("../public/Images/fond.jpg");
    background-size: cover;
    min-height: 15rem;
    font-size: 3rem;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.absolute {
    position: absolute;
    top: 80%;
}

.skill-card {
    max-height: 10rem;
    width: 12%;
    box-shadow: 10px 5px 5px rgb(0, 0, 0);
    border: 1px #FF9408 solid;
}

.skill-card:hover {
    transform: rotate(10deg);
    cursor: pointer;
    border: #FF9408;
}

.skills-content {
    min-height: 20rem;
    /* background: ; */
}

.skills-content :nth-child(2n):hover {
    transform: rotate(-10deg);
}

.contacts {
    padding-bottom: 3rem;
    background-color: #0f0e13;
    background-image: radial-gradient(at 0% 0%, hsla(253, 16%, 7%, 1) 0, transparent 50%), radial-gradient(at 50% 0%, hsla(225, 39%, 30%, 1) 0, transparent 50%), radial-gradient(at 100% 0%, hsla(339, 49%, 30%, 1) 0, transparent 50%);
}

.contact-elt {
    padding: .5rem;
}

.contact-elt i {
    padding-right: .5rem;
}

.switch {
    border: 1px solid #ccd3db;
    border-radius: 15px;
    width: 3rem;
    min-height: 1.3rem;
}

.switch i {
    padding: .2rem;
}

.working {
    font-size: 5rem;
    color: #FF9408;
}