.square {
    min-height: 15rem;
    max-height: 15rem;
    border: .1rem solid #ccc;
    padding: 1rem;
    border-radius: 1rem;
    margin-bottom: 2rem;
}

.square:hover {
    transform: scale(1.1);
    cursor: pointer;
}

.square-body {
    min-height: 8rem;
    font-size: .5rem;
}

.square-head img {
    max-height: 8rem;
}

.bottom-0 {
    bottom: 10px;
    padding-right: 10px;
}

.a-default {
    color: #0489f6 !important;
    font-size: max(1.5vw, 1rem);
}

.a-default:hover {
    transform: scale(1.1);
    border: 1px #0263e0 solid;
    border-radius: 5px;
    padding: 2px;
}

.line {
    background-color: #2e3501;
    height: 4px;
}